import textLogoIcon from '@/assets/logo-text-white.svg';
import Button, { ButtonTheme } from '@/components/button';
import arrowLeftIcon from '@/assets/arrow-left.svg';
import Layout, { LayoutTheme } from '@/components/Layout';
import { useHistory } from '@jupiter/plugin-runtime/router';
import { api } from '@/utils';
import store from 'store';
import { questions, QuestionType, STORAGE_KEYS } from '@/utils/contants';
import Logo from '@/components/Logo';
import Checkbox from '@/components/Checkbox';
import { useState } from 'react';
import styles from './index.module.css';

const Join = () => {
  const history = useHistory();

  const [isJoin, setIsJoin] = useState(true);

  const submit = async () => {
    const info = Object.keys(STORAGE_KEYS).reduce((memo, key) => {
      if (key === 'IDENTITY') {
        // eslint-disable-next-line no-param-reassign
        memo = {
          ...memo,
          ...store.get(STORAGE_KEYS[key]),
        };
      } else {
        // eslint-disable-next-line no-param-reassign
        memo = {
          ...memo,
          // @ts-expect-error
          [key]: store.get(STORAGE_KEYS[key]),
        };
      }

      return memo;
    }, {});

    (info as any).joinInternalTest = isJoin;

    try {
      const { code, msg } = await api.run('update-research-item', info);

      if (code === 200) {
        store.set('has_completed_modern_js_questionnaire', true);
        history.push('/success/1');
      } else {
        history.push('/success/0');
      }
    } catch (_err) {
      history.push('/success/0');
    }
  };

  const toPrevStep = () => {
    history.push(`/questions/${questions.length - 1}`);
  };

  return (
    <Layout
      theme={LayoutTheme.Dark}
      progress="100%"
      backgroundStyle={{
        background: '#262627',
      }}>
      <div className={styles.join}>
        <div className={styles.joinMain}>
          <div className={styles.joinLogo}>
            <Logo theme={LayoutTheme.Dark} />
          </div>
          <p className={styles.joinTitle}>您是否愿意参加 Modern.js 开源内测</p>
          <div className={styles.checkboxGroup}>
            <Checkbox
              label="愿意"
              name="ok"
              type={QuestionType.Checkbox}
              onCheckboxChange={(e: React.BaseSyntheticEvent) => {
                if (e.target.checked) {
                  setIsJoin(true);
                }
              }}
              theme={LayoutTheme.Dark}
              value={isJoin}
              onInputChange={() => {
                /** empty */
              }}
            />
            <Checkbox
              label="不愿意"
              name="no"
              type={QuestionType.Checkbox}
              value={!isJoin}
              theme={LayoutTheme.Dark}
              onCheckboxChange={(e: React.BaseSyntheticEvent) => {
                if (e.target.checked) {
                  setIsJoin(false);
                }
              }}
              onInputChange={() => {
                /** empty */
              }}
            />
          </div>
          <div className={styles.buttonGroup}>
            <Button
              colorTheme={ButtonTheme.Light}
              onClick={() => submit()}
              width={180}
              height={42}
              zIndex={1}>
              提交
            </Button>
            <Button
              colorTheme={ButtonTheme.Dark}
              onClick={() => toPrevStep()}
              width={142}
              height={42}
              zIndex={1}>
              上一步
            </Button>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Join;
